//frontend/pages/messages/index.js
import { useEffect, useState, useRef } from "react";
import api from "../../utils/api";
import { io } from "socket.io-client";

const socket = io("http://localhost:5000");

export default function Messages() {
  const [messages, setMessages] = useState([]);
  const [text, setText] = useState("");
  const [userId, setUserId] = useState(null);
  const [userEmail, setUserEmail] = useState("");
  const bottomRef = useRef(null); // Auto-scroll reference

  useEffect(() => {
    if (typeof window !== "undefined") {
      const id = localStorage.getItem("userId");
      setUserId(id);
      if (id) socket.emit("join_room", id);
    }
  }, []);

  // Auto scroll when messages update
  useEffect(() => {
    if (bottomRef.current) {
      bottomRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [messages]);

  useEffect(() => {
    socket.on("new_message", (msg) => {
      if (String(msg.userId) === String(userId)) {
        setMessages((prev) => [...prev, msg]);
      }
    });
  }, [userId]);

  useEffect(() => {
    if (userId) fetchUserEmail();
  }, [userId]);

  const fetchUserEmail = async () => {
    try {
      const res = await api.get(`/users/${userId}`);
      setUserEmail(res.data.email);
      loadMessages();
    } catch (err) {
      console.error(err);
    }
  };

  const loadMessages = async () => {
    try {
      const res = await api.get(`/messages/${userId}`);
      setMessages(res.data);
    } catch (err) {
      console.error(err);
    }
  };

  const sendMessage = () => {
    if (!text || !userId) return;

    socket.emit("send_message", {
      userId,
      text,
      sender: "user",
    });

    setText("");
  };

  if (!userId)
    return (
      <div className="text-center mt-10 text-xl font-semibold">
        Please login first.
      </div>
    );

  return (
    <div className="h-screen flex flex-col bg-gray-100">

      {/* Header */}
      <div className="bg-blue-600 text-white p-4 shadow-lg flex items-center justify-between">
        <h2 className="text-xl font-semibold">Chat with Admin</h2>

        {/* Home button */}
        <a
          href="/"
          className="bg-white text-blue-700 px-3 py-1 rounded-lg shadow font-semibold"
        >
          Home
        </a>
      </div>

      {/* Chat Box */}
      <div className="flex-1 overflow-y-auto p-4 space-y-3 bg-gradient-to-b from-gray-200 to-gray-100">

        {messages.map((m) => (
          <div
            key={m._id}
            className={m.sender === "user" ? "text-right" : "text-left"}
          >
            <div
              className={
                m.sender === "user"
                  ? "inline-block px-4 py-2 bg-blue-500 text-white rounded-xl rounded-br-none shadow-md"
                  : "inline-block px-4 py-2 bg-white text-gray-900 rounded-xl rounded-bl-none shadow-md border"
              }
            >
              {m.text}
            </div>
          </div>
        ))}

        {/* Auto-scroll target */}
        <div ref={bottomRef}></div>
      </div>

      {/* Message Input */}
      <div className="p-3 border-t bg-white flex items-center gap-2">
        <input
          className="flex-1 border p-3 rounded-xl bg-gray-100 focus:outline-none shadow-inner"
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Type your message..."
        />
        <button
          onClick={sendMessage}
          className="bg-blue-600 text-white px-5 py-3 rounded-xl shadow hover:bg-blue-700"
        >
          Send
        </button>
      </div>
    </div>
  );
}
